import logo from './logo.svg';
import './App.css';
import 'antd/dist/antd.css';
import {Button} from 'antd';
import names from "./constant";

function App() {
    return (
        <div className="App">
            <header className="App-header">
                <img src={logo} className="App-logo" alt="logo"/>
                <div>
                    <div>选择你满意的名称</div>
                    <p/>
                    <div id={"name"} className={"name_content"}>...</div>
                    <p/>
                    <Button type="primary" size="large" shape="round" onClick={() => choose()}>重新选一个</Button>
                    <p/>
                    <div id={"ph"}></div>
                </div>
            </header>
        </div>
    );
}

function choose() {
    let t = document.getElementById("name").innerText
    let ns = names.copyWithin(0, 0).filter((value) => {
        return value !== t
    })

    let index = parseInt(Math.random() * ns.length)
    document.getElementById("name").innerText = ns[index]
    document.getElementById("ph").innerText = "满意么？可以用它给你的项目/产品/平台进行命名吧！"
}

export default App;
